	methods: {
		...mapActions([
			"searchAll",
			"searchRegion",
			"searchHospital",
			"searchDoctor",
			"searchRate",
			"searchRegion_Hospital",
			"searchRegion_Doctor",
			"searchRegion_Doctor_Rate",
